.courses-page {
    padding: 20px;
  }
  
  .subhead {
    margin-bottom: 20px;
  }
  
  .course-list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .course-thumbnail {
    width: 200px;
    height: 200px;
    cursor: pointer;
    background-size: cover;
    background-position: center;
  }
  
  .course-details {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .course-image {
    max-width: 100%;
    height: auto;
  }
  
  .course-info {
    text-align: center;
  }
  
  button {
    margin-top: 20px;
  }